.Navbare {
  text-align: center;
}

.Navbare-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .Navbare-logo {
    animation: Navbare-logo-spin infinite 10s linear;
  }
}

.Navbare-header {
  background-color: #3daf33;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Navbare-link {
  color: #61dafb;
}

@keyframes Navbare-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
